<template>
  <div>
    <div id="navbar">
      <b-navbar toggleable="md" type="dark" variant="info">
        <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
        <b-navbar-brand href="#">Kagami Neko Club</b-navbar-brand>
        <b-nav-text>{{ message }}</b-nav-text>
        <b-collapse is-nav id="nav_collapse">
          <b-navbar-nav class="ml-auto">
            <b-nav-item href="#">Link</b-nav-item>
            <b-nav-item href="https://github.com/kagamiNekoClub">GitHub</b-nav-item>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
      <img src="../assets/logo.png">
    </div>

    <div id="body">
      <h3 align="left" style="color: grey">Projects</h3>
      <hr color=#987cb9 size=3>
      <b-card-group deck class="proj">
        <b-card title="雾海世界"
                img-src="https://kagaminekoclub.github.io/assets/ais.png"
                img-alt="Image"
                img-top
                tag="article"
                style="max-width: 30rem;"s
                class="mb-3"
                align="left">
          <p class="card-text">
            在雾海世界中，万物万事皆由信息构成：事物的形状、颜色、气味、触感、温度、声音……
          </p>
          <b-link href="https://github.com/kagamiNekoClub/All-In-Secret"
                  class="card-link">前往</b-link>
        </b-card>
      </b-card-group>
    </div>

    <div id="footer">
        <h6 class="footer-text">Power by shitake</h6>
    </div>

  </div>
</template>

<script>
  let xmlhttp = new XMLHttpRequest()
  let data = { message: '' }
  xmlhttp.onreadystatechange = _ => {
    if (xmlhttp.readyState === 4 && xmlhttp.status === 200) {
      data.message = xmlhttp.responseText
    }
  }

  xmlhttp.open('GET', 'https://sslapi.hitokoto.cn/?c=abc&encode=text', true)
  xmlhttp.send()
  export default {
    name: 'main',
    data: function () {
      return data
    }
  }
</script>

<style scoped>

  #body {
    margin-left: 160px;
    margin-right:160px;
  }

  #footer {
    height: 100px;
    color: grey;
    text-align: center;
  }

  .footer-text {
    line-height: 100px;
  }

</style>



